*, *::before, *::after {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root {
    --black-color: #000000;
    --white-color: #ffffff;
    --blue-color: #0c09d6;
    --text-color: #1f1f1f;

    --container-width-lg: 80%;
    --container-width-md: 90%;

    --transition: all 500ms ease;
}

body {
    font-family: 'Roboto', sans-serif;
    color: var(--text-color);
    line-height: 1.7;
    overflow-x: hidden;
    background: var(--white-color);
}

.container {
    width: var(--container-width-lg);
    max-width: 1920px;
    margin-inline: auto;
}

h1, h2, h3, h4, h5 {
    line-height: 1.2;
    color: var(--blue-color);
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

a {
    color: var(--black-color);
}

img {
    display: block;
    object-fit: cover;
    width: 100%;
}

.btn {
    color: var(--white-color);
    background: var(--black-color);
    font-size: 0.9rem;
    padding: 0.4rem 1.2rem;
    border: var(--black-color) 2px solid;
    border-radius: 5px;
    transition: var(--transition);
}

.btn:hover {
    background: transparent;
    color: var(--black-color);
}

.btn.lg {
    padding: 0.7rem 1.8rem;
    font-size: 1.1rem;
}

.text__header {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.text__header div::after {
    content: "";
    display: block;
    width: 40px;
    height: 2px;
    background-color: var(--blue-color);
}

.card {
    padding: 2rem;
    color: var(--black-color);
    background: var(--white-color);
    border: var(--black-color) 1px solid;
    margin-top: 1rem;
    border-radius: 10px;
    transition: var(--transition);
}

.card span {
    display: grid;
    place-content: start;
    margin-bottom: 1rem;
}

.card span svg {
    padding: .5rem;
    width: 3rem;
    height: 3rem;
    background: var(--black-color);
    color: var(--white-color);
    border-radius: 50%;
}

.card.active {
    background: var(--black-color);
    color: var(--white-color);
}

.card.active span svg {
    background: var(--white-color);
    color: var(--black-color);
}

.card h4 {
    margin-bottom: 1rem;
}

.card:hover {
    background: var(--black-color);
    color: var(--white-color);
}

.card:hover span svg {
    background: var(--white-color);
    color: var(--black-color);
}

.card.active:hover {
    background: var(--white-color);
    color: var(--black-color);
}

.card.active:hover span svg {
    background: var(--black-color);
    color: var(--white-color);
}