nav {
    height: 5rem;
    width: 100vw;
    background: var(--white-color);
    display: grid;
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}

.nav__toggle-btn {
    display: none;
}

.nav__container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.logo {
    width: 7rem;
}

.nav__links {
    display: flex;
    gap: 3.5rem;
    align-items: center;
}

.nav__links a {
    transition: var(--transition);
}

.nav__links a:hover {
    color: var(--blue-color);
}

.active-nav {
    position: relative;
    /* color: var(--blue-color); */
}

.active-nav::after {
    content: '';
    display: block;
    width: 1.8rem;
    height: 2px;
    background: var(--blue-color);
    position: absolute;
    left: 5;
    margin-top: 1.2rem;
}

/* MEDIA QUERIES */

@media screen and (max-width: 768px) {
    .nav__toggle-btn {
        display: inline-block;
        background: transparent;
        font-size: 1.8rem;
        cursor: pointer;
    }
    .nav__toggle-btn svg {
        color: var(--black-color);
    }
    .nav__links {
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap: 0;
        perspective: 400px;
    }
    .active-nav {
        display: none;
    }
    .nav__links li {
        height: 3rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgb(245, 227, 227);
        animation: navAnimation 600ms ease forwards;
        transform: rotate(90deg);
        opacity: 0;
        transform-origin: top;
    }
    .nav__links li:nth-child(2) {
        animation-delay: 200ms;
    }
    .nav__links li:nth-child(3) {
        animation-delay: 400ms;
    }
    .nav__links li:nth-child(4) {
        animation-delay: 600ms;
    }
    .nav__links li:nth-child(5) {
        animation-delay: 800ms;
    }
    @keyframes navAnimation {
        to {
            transform: rotateX(0);
            opacity: 1;
        }
    }
    .nav__links li a {
        color: var(--white-color);
        background-color: var(--black-color);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }
    .show__nav {
        display: flex;
    }

    .hide__nav {
        display: none;
    }
}