.main__header {
    width: 100vw;
    /* height: 100vh; */
    display: grid;
    place-items: center;
    margin-top: 5rem;
}

.main__header-container {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.main__header-left {
    /* margin-top: -1rem; */
}
.main__header-left h1 {
    font-size: 4.5rem;
}

.main__header-left p {
    margin: .5rem 0 2rem 0;
}

.main__header-right {
    display: grid;
    place-items: center;
    position: relative;
}

.banner__chart1 {
    width: 120px;
    background: var(--black-color);
    padding: .5rem;
    background: transparent;
    position: absolute;
    background-color: rgba(22, 22, 22, 0.4);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    border-radius: 10px;
    bottom: 0;
    left: 3rem;
    z-index: 1;
}

.main__header-image {
    position: relative;
    width: 350px;
    /* height: 300px; */
}

.banner__chart2 {
    width: 130px;
    position: absolute;
    top: 0;
    right: 2rem;
}

.header__end {
    display: grid;
    grid-template-columns: 10% 30% 60%;
    /* margin-top: 3rem; */
    align-items: center;
}

.header__end-start::after {
    content: "";
    width: 100%;
    display: block;
    height: 1px;
    background-color: var(--black-color);
}

.header__end p {
    font-size: 12px;
    text-align: center;
}

.header__end-end::before {
    content: "";
    width: 100%;
    display: block;
    height: 1px;
    background-color: var(--black-color);
}

.header__clients {
    display: grid;
    grid-template-columns: repeat(5, auto);
    /* gap: 5rem; */
    align-items: center;
    justify-content: space-between;
    margin-top: 2rem;
}

/*=============== Home About Section ================== */
.home__about {
    margin-top: 5rem;
}

.home__about-container {
    display: grid;
    grid-template-columns: 40% 50%;
    gap: 10%;
}

.home__about-text h2 {
    margin: 1rem 0;
}

.home__about-text .about__last-p {
    margin-bottom: 2rem;
}

.home__about-image img {
    border-radius: 10px;
}


/*=============== Home Jobs Section ================== */
.jobs {
    margin-top: 5rem;
}

.template__container {
    margin-top: 2rem;
}

.searchInput__container {
    display: flex;
    justify-content: center;
}

.searchInput__container input {
    border: var(--black-color) 1px solid;
    padding: 1rem 4rem 1rem 0.5rem;
    border-radius: 10px;
    width: 80%;
}

.template__containers {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 2rem;
    position: relative;
}

.template {
    padding: 1rem;
    border: var(--black-color) 1px solid;
    position: relative;
    border-radius: 10px;
}

.template h4 {
    margin-bottom: 1rem;
}

.template p {
    margin-top: 1rem;
    font-size: 14px;
}

.template p:nth-child(3) {
    margin-bottom: 3rem;
}

.template .btn {
    position: absolute;
    bottom: 0.5rem;
}

.pagination__container {
   
    margin-top: 3rem;

}

.pagination {
    width: 100%;
    display: flex;
    justify-content: center;
}

.pagination a {
    padding: 10px;
    margin: 8px;
    border-radius: 10px;
    border: var(--blue-color) 1px solid;
    color: var(--blue-color);
    cursor: pointer;
    transition: var(--transition);
}

.pagination a:hover {
    color: var(--white-color);
    background: var(--blue-color);
}

.paginationActive a {
    color: var(--white-color);
    background: var(--blue-color);
}



/*=============== Home Services Section ================== */
.services__section {
    margin-top: 5rem;
}

.services__container {
    display: grid;
    grid-template-columns: 45% 50%;
    gap: 5%;
}

.services__right {
    display: block;
    margin-top: 3rem;
}

.services__right span {
    display: grid;
    place-content: start;
    margin: 1rem 0;
}

.services__right span svg {
    padding: .5rem;
    width: 3rem;
    height: 3rem;
    background: var(--black-color);
    color: var(--white-color);
    border-radius: 50%;
}

.services__right h2 {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.services__right p {
    margin-bottom: 2rem;
}

/*=============== Home What Sets Us Apart Section ================== */
.set__apart {
    margin-top: 5rem;
}

.set__apart-container {
    display: block;
    position: relative;
}

.set__top-side {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.set__image-middle {
    margin-top: 1rem;
}

.set__image-middle img {
    width: 100%;
    height: 400px;
    border-radius: 10px;
}

.set__apart-last {
    margin-top: 2rem;
    text-align: center;
    display: ;
}

.set__apart-last p {
    text-align: center;
    margin: 0 2rem 1rem 2rem;
}


/* =============== Testimonials =============== */
.testimonials {
    margin-top: 5rem;
}

.testimonial__top {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
}

.testimonial__top-left img {
    border-radius: 10px;
}

.testimonial__top-right {
    display: block;
}

.testimonial__top-right h2 {
    margin-top: 1rem;
}

.testimonial__sec {
    width: 40rem;
    margin-inline: auto;
    margin-top: -10rem;
}

.testimonials__head {
    margin-top: 5rem;
}

.card.testimonial {
    position: relative;
    text-align: left;
    margin-top: 5rem;
}

.testimonial__avatar {
    width: 4rem;
    height: 4rem;
    border-radius: 1.5rem;
    overflow: hidden;
    position: absolute;
    top: -2rem;
    left: calc(50% - 2rem);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    transition: var(--transition);
}

.testimonial:hover .testimonial__avatar {
    border-radius: 50%;
    border-color: var(--blue-color);
}

.testimonial__quote {
    font-style: italic;
    margin: 1rem 0 2rem;
}

.card small.testimonial__title {
    margin-top: 0.3rem;
}

.testimonials__btn-container {
    width: fit-content;
    margin: 1rem auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.testimonials__btn {
    background: transparent;
    cursor: pointer;
}

.testimonials__btn svg {
    fill: var(--black-color);
    font-size: 1rem;
    width: 2rem;
    height: 2rem;
}

/* ============= Newsletter Section ============== */
.news__letter {
    margin-top: 5rem;
}

.newsletter__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: var(--black-color);
    padding: 3rem;
    border-radius: 1.5rem;
}

.newsletter__left {
    margin-top: 3rem;
}

.newsletter__left h2 {
    margin-top: 1rem;
    color: var(--white-color);
}

.newsletter__left {
    margin-top: 2rem;
    position: relative;
}

.newsletter__form {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.newsletter__form input {
    padding: .6rem 0;
    width: 100%;
    position: relative;
    border-radius: 5px;
}

.newsletter__form .btn {
    margin: 1rem;
    position: relative;
    color: var(--white-color);
    background-color: var(--black-color);
    border: 2px solid var(--white-color);
}
.newsletter__form .btn:hover {
    color: var(--black-color);
    background-color: var(--white-color);
}

.newsletter__left small {
    font-size: 11px;
    color: var(--white-color);
}

.newsletter__left a {
    color: var(--blue-color);
} 

.newsletter__right {
    position: relative;
}

.newsletter__main-image img {
    border-radius: 10px;
}

.newsletter__chart-image {
    position: relative;
}

.newsletter__chart-image img {
    position: absolute;
    width: 10rem;
    top: -12rem;
    left: -5rem;
    border-radius: 10px;
}

/* =========== Footer Section ================= */
.footer__section {
    margin-top: 5rem;
    background-color: var(--black-color);
}

.footer__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 3rem;
    padding-top: 3rem;
}

.footer__about p {
    color: var(--white-color);
}

.footer__social {
    display: flex;
    align-items: center;
    margin-top: 2rem;
    gap: 3rem;
}

.footer__social span {
    font-size: 1.5rem;
    color: var(--white-color);
    cursor: pointer;
    transition: var(--transition);
}

.footer__social span:hover {
    color: var(--blue-color);
}

.footer__link {
    
}

.footer__link h4 {
    text-align: center;
}

.footer__link li {
    display: flex;
    flex-direction: column;
    /* margin-left: 45%; */
    align-items: center;
    /* justify-content: flex-start; */
    color: var(--white-color);
    line-height: 3rem;
}

.footer__services h5 {
    margin-top: 1rem;
}

.footer__services p {
    color: var(--white-color);
    margin-top: 1rem;
}

.footer__copy {
    margin-top: 2rem;
    border-top: var(--white-color) 1px solid;
}

.footer__copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.footer__copyright p {
    color: var(--white-color);
    text-align: center;
}

.footer__copyright a {
    color: var(--blue-color);
    transition: var(--transition);
}

.footer__copyright a:hover {
    color: var(--white-color);
}



/* ============ Media BreakPoints =============== */

@media only screen and (max-width: 992px) {
    .main__header-left h1 {
        font-size: 4rem;
    }
    .banner__chart1 {
        width: 100px;
        left: 0;
    }
    .banner__chart2 {
        width: 100px;
        right: 0;
    }
    .home__about-text h2 {
        font-size: 1.8rem;
    }
    .template__containers {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .testimonials__head {
        margin-top: 1rem;
    }
    .testimonial__top-right h2 {
        font-size: 1.5rem;
    }
    .testimonial__sec {
        width: 30rem;
    }
    .newsletter__container {
        gap: 4rem;
    }
    .newsletter__left {
        margin-top: 0;
    }
    .newsletter__left h2 {
        font-size: 1.5rem;
    }
    .newsletter__chart-image img {
        position: absolute;
        width: 5rem;
        top: -10rem;
        left: -3rem;
    }
}

@media only screen and (max-width: 900px) {
    .newsletter__container {
        grid-template-columns: 1fr;
    }
    .newsletter__left h2 {
        text-align: center;
    }
    .newsletter__form {
        margin: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        position: relative;
        place-content: center;
    }
    .newsletter__left small {
        position: relative;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2px;
    }
    .newsletter__main-image img {
    
        position: relative;
        /* margin-left: 8rem; */
    }
    .newsletter__chart-image img {
        display: none;
    }
}

@media only screen and (max-width: 768px) {
    .main__header-container {
        grid-template-columns: 1fr;
    }
    .main__header-left {
        text-align: center;
        margin-top: 3rem;
    }
    .main__header-right {
        margin-top: 2rem;
    }
    .banner__chart1 {
        margin-left: 4rem;
    }
    .banner__chart2 {
        margin-right: 8rem;
    }
    .home__about-container {
        grid-template-columns: 1fr;
        text-align: center;
    }
    .about__header1 {
        display: grid;
        place-content: center;
    }
    .jobs {
        margin-top: 10rem;
    }
    .template__containers {
        grid-template-columns: 1fr 1fr;
    }
    .services__container {
        display: flex;
        flex-direction: column-reverse;
    }
    .services__right span {
        place-content: center;
    }
    .services__right {
        text-align: center;
    }
    .services__right h2 {
        text-align: center;
    }
    .card {
        text-align: center;
    }
    .services__left .card span {
        place-content: center;
    }
    .testimonials {
        margin-top: 5rem;
    }
    .testimonial__top-right h2 {
        text-align: center;
    }
    .testimonial__top {
        display: flex;
        flex-direction: column-reverse;
        gap: 3rem;
    }
    .newsletter__container {
        gap: 2rem;
    }
    .newsletter__container {
        grid-template-columns: 1fr;
    }
 /*    .newsletter__left {
        display: flex;
        place-content: center;
    } */
    .newsletter__left h2 {
        text-align: center;
    }
    .newsletter__form {
        margin: 1rem;
        position: relative;
        place-content: center;
    }
    .newsletter__left small {
        top: -1rem;
        position: relative;
        display: flex;
        justify-content: center;
      }
    .newsletter__right {
        /* margin-top: 2rem; */
        position: relative;
        display: grid;
        place-content: center;
    }
    .footer__container {
        grid-template-columns: 1fr;
    }
    .footer__about {
        text-align: center;
    }
    .footer__social {
        justify-content: center;
    }
    .footer__services {
        text-align: center;
    }
}

@media only screen and (max-width: 600px) {
    .banner__chart1 {
        margin-left: 2rem;
    }
    .banner__chart2 {
        margin-right: 4rem;
    }
    .testimonials {
        margin-top: 5rem;
    }
    .testimonial__sec {
        width: 80%;
    }
}

@media only screen and (max-width: 500px) {
    .banner__chart2 {
        margin-right: 2rem;
    }
    .template__containers {
        grid-template-columns: 1fr;
    }
    .template .btn {
        
    }
    .newsletter__form input {
        padding: 5%;
    }
    .newsletter__left small {
        flex-direction: column;
    }
}
